import { createSlice, PayloadAction } from "@reduxjs/toolkit"

export enum TradingInterval {
  min1 = '1min', 
  min5 = '5min', 
  min15 = '15min', 
  min30 = '30min', 
  hr1 = '1hr', 
  hr2 = '2hr', 
  hr4 = '4hr', 
  hr12 = '12hr', 
  d1 = '1d', 
  w1 = '1w',
}

export interface TradingInfo {
  symbol: string
  interval: TradingInterval
}

const initialState = { symbol: 'LRC-USDT', interval: TradingInterval.d1 } as TradingInfo;

const tradingSlice = createSlice({
  name: "trading",
  initialState,
  reducers: {
    changeSymbol(state, action: PayloadAction<any>) {
        state.symbol = action.payload;
    },
    changeTradingInterval(state, action: PayloadAction<any>) {
        state.interval = action.payload;
    },
  },
})

export const { changeSymbol, changeTradingInterval } = tradingSlice.actions
export default tradingSlice
