import { useEffect } from 'react'

import styled from 'styled-components/macro'

import * as echarts from 'echarts'

export interface ChartProps {
    defaultOption: any,
    getOption: any,
}

const ChartDiv = styled.div`
width: 100%;
height: 100%;
`

const Chart = ({ defaultOption, getOption }: ChartProps) => {

    const chart_ref = 'trading_chart'

    var myChart: any = null

    useEffect(() => {
        const chartDom: any = document.getElementById(chart_ref)
        myChart = echarts.init(chartDom, 'dark')
        myChart.setOption(defaultOption);

        const id = setInterval(async () => {
            if (myChart) {
                myChart.setOption(await getOption())
            }
        }, 1000)
        return () => clearInterval(id)
    });

    return (
        <ChartDiv id={chart_ref}></ChartDiv>
    )
};

export default Chart;
